  border-radius: 12px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  transition-property: background-color, color;
  transition-duration: .25s, .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1), cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.location-modal__city-btn:hover, .location-modal__city-btn:focus, .location-modal__language-btn:hover, .location-modal__language-btn:focus {
  color: #fff;
