.location-modal-window__close-btn {
  width: 24px;
  height: 24px;
  background-color: #f5f5f7;
  border: 0;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.location-modal-window__close-btn:hover, .location-modal-window__close-btn:focus {
  background-color: #f63;
}

.location-modal__city, .location-modal__language {
  flex-wrap: wrap;
  gap: 12px;
  display: flex;
}

.location-modal__language-btn.selected, .location-modal__city-btn.selected {
  color: #fff;
  background-color: #f63;
}

.location-modal-window__close-btn-icon {
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.location-modal-window__close-btn:hover .location-modal-window__close-btn-icon, .location-modal-window__close-btn:focus .location-modal-window__close-btn-icon {
  fill: #fff;
}

@media screen and (min-width: 375px) {
  .backdrop {
    display: none;
  }
}

@media screen and (min-width: 1576px) {
  .backdrop {
    width: 100%;
    height: 100%;
    opacity: 1;
    background: rgba(0, 0, 0, .2);
    transition-property: opacity;
    transition-duration: .5s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    display: block;
    position: fixed;
    top: 0;
    left: 0;
  }
}

.hide {
  opacity: 0;
  pointer-events: none;
}

.backdrop .meditation {
  transform: translate(-50%, -50%);
}

.meditation {
  width: 600px;
  height: 700px;
  background: #fff;
  border-radius: 12px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.meditation__close-btn {
  background-color: inherit;
  border: 0;
  position: absolute;
